"use client";

import { useState } from "react";
import { liveClasses } from "@/data/classes";

export default function LiveClasses() {
  const [selectedClass, setSelectedClass] = useState<typeof liveClasses[0] | null>(null);
  const [filter, setFilter] = useState("all");

  const categories = Array.from(
    new Set(liveClasses.map((course) => course.category))
  );

  const filteredClasses = filter === "all" 
    ? liveClasses 
    : liveClasses.filter(course => course.category === filter);

  const joinClass = (course: typeof liveClasses[0]) => {
    window.open(course.meetingUrl, '_blank');
  };

  return (
    <div className="space-y-8">
      {/* Header */}
      <div className="border-b pb-5">
        <h1 className="text-4xl font-bold text-gray-900">Live Classes</h1>
        <p className="mt-2 text-lg text-gray-600">
          Join interactive live sessions with industry experts
        </p>
      </div>

      {/* Filters */}
      <div className="flex flex-wrap gap-2">
        <button
          onClick={() => setFilter("all")}
          className={`px-4 py-2 text-sm font-medium rounded-full transition-colors ${
            filter === "all"
              ? "bg-gray-900 text-white"
              : "bg-gray-100 text-gray-700 hover:bg-gray-200"
          }`}
        >
          All Classes
        </button>
        {categories.map((category) => (
          <button
            key={category}
            onClick={() => setFilter(category)}
            className={`px-4 py-2 text-sm font-medium rounded-full transition-colors ${
              filter === category
                ? "bg-gray-900 text-white"
                : "bg-gray-100 text-gray-700 hover:bg-gray-200"
            }`}
          >
            {category}
          </button>
        ))}
      </div>

      {/* Course Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredClasses.map((course) => (
          <div
            key={course.id}
            className="bg-white rounded-lg shadow-sm border border-gray-200 overflow-hidden hover:shadow-md transition-all duration-200"
          >
            <div className="relative">
              <img
                src={course.thumbnail}
                alt={course.title}
                className="w-full h-48 object-cover"
              />
              <div className="absolute top-2 right-2">
                <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                  {course.category}
                </span>
              </div>
            </div>
            
            <div className="p-4 space-y-4">
              <div>
                <h3 className="text-lg font-semibold text-gray-900">
                  {course.title}
                </h3>
                <p className="mt-1 text-sm text-gray-500">
                  by {course.instructor}
                </p>
              </div>

              <div className="space-y-2">
                <div className="flex items-center text-sm text-gray-500">
                  <svg className="w-4 h-4 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7V3m8 4V3m-9 8h10M5 21h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v12a2 2 0 002 2z" />
                  </svg>
                  {course.date} at {course.time}
                </div>
                <div className="flex items-center text-sm text-gray-500">
                  <svg className="w-4 h-4 mr-1" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                  {course.duration}
                </div>
              </div>

              <p className="text-sm text-gray-600 line-clamp-2">
                {course.description}
              </p>

              <div className="space-y-2">
                <button
                  onClick={() => joinClass(course)}
                  className="w-full px-4 py-2 text-sm font-medium text-white bg-gray-900 rounded-md hover:bg-gray-800 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-900 transition-colors duration-200"
                >
                  Join Live Class
                </button>
                <button
                  onClick={() => setSelectedClass(course)}
                  className="w-full px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 rounded-md hover:bg-gray-200 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-500 transition-colors duration-200"
                >
                  View Details
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Course Details Modal */}
      {selectedClass && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
          <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
            <div className="p-6 space-y-6">
              <div className="flex justify-between items-start">
                <h2 className="text-2xl font-bold text-gray-900">
                  {selectedClass.title}
                </h2>
                <button
                  onClick={() => setSelectedClass(null)}
                  className="text-gray-400 hover:text-gray-500"
                >
                  <svg className="w-6 h-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                </button>
              </div>

              <img
                src={selectedClass.thumbnail}
                alt={selectedClass.title}
                className="w-full h-64 object-cover rounded-lg"
              />

              <div className="space-y-4">
                <div>
                  <h3 className="font-semibold text-gray-900">Instructor</h3>
                  <p className="text-gray-600">{selectedClass.instructor}</p>
                </div>

                <div>
                  <h3 className="font-semibold text-gray-900">Schedule</h3>
                  <p className="text-gray-600">
                    {selectedClass.date} at {selectedClass.time}
                  </p>
                  <p className="text-gray-600">Duration: {selectedClass.duration}</p>
                </div>

                <div>
                  <h3 className="font-semibold text-gray-900">Description</h3>
                  <p className="text-gray-600">{selectedClass.description}</p>
                </div>

                {selectedClass.prerequisites && (
                  <div>
                    <h3 className="font-semibold text-gray-900">Prerequisites</h3>
                    <ul className="list-disc list-inside text-gray-600">
                      {selectedClass.prerequisites.map((prereq, index) => (
                        <li key={index}>{prereq}</li>
                      ))}
                    </ul>
                  </div>
                )}

                {selectedClass.materials && (
                  <div>
                    <h3 className="font-semibold text-gray-900">Course Materials</h3>
                    <ul className="list-disc list-inside text-gray-600">
                      {selectedClass.materials.map((material, index) => (
                        <li key={index}>{material}</li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>

              <button
                onClick={() => joinClass(selectedClass)}
                className="w-full px-4 py-2 text-sm font-medium text-white bg-gray-900 rounded-md hover:bg-gray-800 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-900 transition-colors duration-200"
              >
                Join Live Class
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
